import React from "react";
import { Button } from "@material-ui/core";
import FetchDataReturn from "../helpers/fetchDataReturn";
import DeleteServerImage from "../helpers/deleteServerImage";

const DeletePiece = ({ name, setPieces, deleteImages }) => {
  async function fetchNSet() {
    const data = await FetchDataReturn("/api/pieces");
    setPieces((prevState) => ({ ...prevState, pieces: data }));
  }

  const deletePiece = async (deleteImages) => {
    function getImageName(path) {
      let url = path;
      let stringCutStart = url.split("/");
      return stringCutStart[stringCutStart.length - 1];
    }

    function deleteAllImages(deleteImages) {
      try {
        deleteImages.images.map((value) =>
          DeleteServerImage(getImageName(value.images))
        );
      } catch (e) {
        console.log(
          `Error: ${e}\nYou need to refresh the page before being able to delete the images off the server cause i wrote this when i was learning and state isn't being fully set in the right place.`
        );
      }
    }

    async function deleteJustPiece() {
      await fetch(`/api/pieces/delete-piece`, {
        method: "delete",
        body: JSON.stringify({ name: name }),
        headers: {
          "Content-Type": "application/json",
        },
      }).then((response) => {
        console.log(response);
        fetchNSet();
      });
    }

    function deleteEntirePiece(deleteImages, cb) {
      if (deleteImages.images) {
        deleteAllImages(deleteImages);
      }

      if (deleteImages.header) {
        DeleteServerImage(getImageName(deleteImages.header));
      }

      if (deleteImages.thumbnail) {
        DeleteServerImage(getImageName(deleteImages.thumbnail));
      }

      cb();
    }

    deleteEntirePiece(deleteImages, deleteJustPiece);
  };

  return <Button onClick={() => deletePiece(deleteImages)}>Delete</Button>;
};

export default DeletePiece;
